@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap");
body {
    // overflow-x: hidden;
    font-family: "Montserrat", sans-serif;
}
.costItem_content {
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: minmax(14rem, 1fr) 3rem minmax(20rem, 4fr);
    grid-template-columns: minmax(14rem, 1fr) minmax(20rem, 4fr);
    gap: 3rem;
    margin: 3rem 2rem 3rem;

    .settings--none{
        display: none !important;
        opacity: 0 !important;
    }

    .inputFormRequest {
        width: 40vw;
        display: flex;
        position: relative;
        justify-content: flex-end;

        .button {
            cursor: pointer;
            padding: 1.3rem 2rem;
            position: absolute;
            top: 1rem;
        }
    }
    .selectric-wrapper {
        width: 40vw !important;
        .selectric-scroll {
            height: 10rem !important;
            background-color: #fff;
        }
    }

    .input,
    .textarea,
    .label,
    li {
        font-family: "Montserrat", sans-serif;
        font-weight: 500 !important;
    }
    small {
        font-weight: 700;
        font-size: 0.8rem !important;
    }
    .label--sm {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        margin-bottom: 3vw;
        font-size: 1rem;
        color: #9b9b9b;
    }
    .label--mar {
        margin: 2vw 0 !important;
    }
    .label--left {
        font-weight: 700;
    }
    .label--left--load {
        margin-top: 1rem;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }

    .item--add {
        width: 8px;
        cursor: pointer;
        color: #fff;
        position: relative;
        background-color: #6ab04c;
        padding: 1px 9px 4px 8px;
        border-radius: 50%;
        font-weight: 900;
        right: -3vw;
    }
    .item--add--open {
        transition: 0.3s all ease-in-out;
        -webkit-transform: rotateZ(180deg) !important;
        -ms-transform: rotate(180deg) !important;
        transform: rotateZ(180deg) !important;
        width: 8px !important;
        right: 0 !important;
    }
    .item--add {
        transition: 0.3s all ease-in-out;
        -webkit-transform: rotateZ(270deg);
        -ms-transform: rotate(270deg);
        transform: rotateZ(270deg);
    }

    .settings {
        transition: 1s all ease-in-out;
        margin-right: 2rem;
        display: grid;
        row-gap: 3rem;
        grid-template-columns: repeat(2, minmax(10.625rem, 1fr));
        column-gap: 2.7rem;
        input[type="number"]::-webkit-outer-spin-button,
        input[type="number"]::-webkit-inner-spin-button {
            -webkit-appearance: none;
            margin: 0;
            -moz-appearance: textfield;
        }
        input[type="date"]::-webkit-calendar-picker-indicator {
            margin-right: 1rem;
        }
        .settings--inputElement {
            font-size: 0.9rem;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
        }
    }

    .checkbox {
        display: flex;
        cursor: pointer;
        -webkit-user-select: none;
        user-select: none;
        position: relative;
        flex-direction: column;
        justify-content: space-around;
        row-gap: 0.5rem;
        .label{
            font-size: 0.9rem;
        }
        input {
            opacity: 0;
            display: none;
        }
        input[type="checkbox"]:checked + label,
        input[type="checkbox"]:not(:checked) + label {
            display: inline-block;
            position: relative;
            padding-left: 28px;
            line-height: 20px;
            cursor: pointer;
        }

        input[type="checkbox"]:checked + label:before,
        input[type="checkbox"]:not(:checked) + label:before {
            transition: 0.3s all ease-in-out;
            content: "";
            position: absolute;
            left:0;
            width: 1rem;
            height: 1rem;
            border: 1px solid #6ab04c;
            border-radius: 4px;
        }
        input[type="checkbox"]:checked + label:before {
            content: "\2713";
            display: flex;
            font-weight: 900;
            font-size: 1.6rem;
            color: #6ab04c;
            align-items: flex-end;
            justify-content: flex-start;
        }
        input[type="checkbox"]:checked + label:after {
            opacity: 1;
        }
    }
}

.label {
    margin: 1rem 0 !important;
}
.input {
    margin: 1rem 0 0 !important;
}

.costItem_content {
    li {
        font-size: 0.8rem !important;
        text-transform: none !important;
    }
}

@media (min-width: 1080px) {
    .costItem_content {
        margin: 3rem auto 0 !important;
        max-width: 1000px !important;
    }
}

@media (max-width: 768px) {
    .costItem_content {
        margin-left: 3vw;
        -webkit-column-gap: 2rem !important;
        -moz-column-gap: 2rem !important;
        column-gap: 2rem !important;
        -ms-grid-columns: minmax(10rem, 10rem) 2rem minmax(20rem, 4fr);
        grid-template-columns: minmax(10rem, 10rem) minmax(20rem, 4fr);
        .settings {
            grid-template-columns: repeat(2, minmax(8rem, 1fr));
            row-gap: 2rem;
        }
        .item--add {
            margin-left: 1rem;
        }
        .inputFormRequest {
            width: 60vw !important;
        }
    }
}
@media (max-width: 560px) {
    .costItem_content {
        width: 90vw !important;
        margin-left: 2vw !important;
        margin-right: 2vw !important;
        -ms-grid-columns: 80vw !important;
        grid-template-columns: 80vw !important;
        padding-left: 1rem;
        .item--add {
            right: 2rem !important;
        }
        .item--add {
            transition: 0.3s all ease-in-out;
            -webkit-transform: rotateZ(180deg) !important;
            -ms-transform: rotate(180deg) !important;
            transform: rotateZ(180deg) !important;
            right: -2rem !important;
        }
        .item--add--open {
            transition: 0.3s all ease-in-out;
            -webkit-transform: rotateZ(270deg) !important;
            -ms-transform: rotate(270deg) !important;
            transform: rotateZ(270deg) !important;
            right: 1rem !important;
        }
        .inputFormRequest {
            width: 78vw !important;
        }
    }
}
